<template>
  <div class="notification-item" @click="redirectToNotification">
    <img :src="notificationData.actor_avatar" class="notification-item__image">
    <div class="notification-item__data-wrapper">
      <div class="notification-item__data">
        <div v-if="notificationData.type_notification==='change_log'">
          <b class="notification-item__display-name">{{ "Cloudjet KPI updated new version" | translate }}: </b>
          <span class="notification-item__version">{{ "Version" | translate }} {{notificationData.version}} </span>
          <span class="notification-item__release-date">{{ "updated" | translate }} {{notificationData.release_date}} </span>
        </div>

        <div v-else>
          <b class="notification-item__display-name">{{notificationData.actor_name}}: </b>
          <span class="notification-item__content-action" v-html="ellipisDescription"></span>
        </div>
      </div>
      <div class="notification-item__diffTime">{{diffTimeToString}}</div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    ellipisDescription() {
      let descriptionSplitN = this.notificationData.description.split("\n");
      if (descriptionSplitN.length > 1) {
        /**
         * Todo
         * Backend fix: 2 dòng thì thêm ...
         */
        if (descriptionSplitN[0].length < 5) {
          return descriptionSplitN[0] + descriptionSplitN[1] + "...";
        } else {
          return descriptionSplitN[0];
        }
      } else {
        return this.notificationData.description;
      }
    },

    /**
     * Diff = now - timestamp
     * Business:
     */
    diffTimeToString() {
      var now = new Date();
      var past = new Date(this.notificationData.timestamp);
      var diffTimeInMs = now.getTime() - past.getTime();
      var diffmin = diffTimeInMs / (1000 * 60);
      var diffHours = diffmin / 60;
      var diffDay = diffHours / 24;

      if (diffDay >= 1.5) {
        return (
          past.getDate() +
          "/" +
          (past.getMonth() + 1) +
          "/" +
          past.getFullYear() +
          " at " +
          past.getHours() +
          ":" +
          past.getMinutes() +
          ":" +
          past.getSeconds()
        );
      }

      if (diffDay < 1.5 && diffDay >= 1) {
        return parseInt(diffDay) + " day ago";
      }

      if (diffHours >= 1) {
        return parseInt(diffHours) + " hours ago";
      }

      if (diffmin < 60 && diffmin >= 1) {
        return parseInt(diffmin) + " minutes ago";
      } else {
        return "recent";
      }
    }
  },
  props: {
    /**
     * Link redirect when click notification item
     */
    href: String,
    /**
     * Notification object api
     */
    notificationData: Object,

    /**
     * Notification number,
     * After click will set notification Number to zero
     * Only display when notification bigger than 0
     */
    notificationCount: Number
  },
  methods: {
    /**
     * Unit Test should set window location to hreff
     */
    redirectToNotification() {
      debugger;
      global.window.location.href = this.href;
    }
  }
};
</script>

